.itemTile {
  width: 280px;
  transition: 0.2s linear;
}

.itemTile > .itemPreview {
  width: 280px;
  height: 160px;
  position: relative;
  top: 0;
  left: 0;
}

.itemTile > .itemPreview > .previewHero {
  height: 100%;
  border-radius: 8px;
}

.itemTile > .itemPreview > .previewHero img {
  border-radius: 8px;
}

.itemTile > .itemPreview > .previewButtons {
  display: none;
  height: 25%;
}

.itemTile > .itemPreview > .previewButtons img {
  width: 20px;
  height: 20px;
}

/* Hover Section */

.itemTile:hover {
  transform: scale(1.4);
  transition: 0.2s linear;
}

.itemTile:hover > .itemPreview > .previewHero {
  height: 75%;
  border-radius: 8px;
}

.itemTile:hover > .itemPreview > .previewButtons {
  display: flex;
  height: 25%;
}

.casualSlider .swiper-slide:hover {
  z-index: 99;
}

@media (width < 580px) {
    .itemTile {
      transform: scale(0.93);
    }
  
    .itemTile > .itemPreview {
      transform: scale(0.93);
    }
  
    .itemTile:hover {
      transform: scale(1.2);
    }
}

@media (width < 480px) {
    .itemTile {
      transform: scale(0.85);
    }
  
    .itemTile > .itemPreview {
      transform: scale(0.75)
    }
  
    .itemTile:hover {
      transform: scale(1.2);
    }
}
