.mainNavigationHeader {
  display: flex;
  width: 100vw;
  height: 60px;
  padding: 20px;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 2;
  background: linear-gradient(
    180deg,
    rgba(20, 20, 20, 1) 10%,
    rgba(113, 235, 242, 0) 100%
  );
}

.logo {
  width: 90px;
  height: 23px;
  margin: 0 10px 0 30px;
  object-fit: contain;
}

.mainMenu {
  width: 500px;
  display: flex;
  justify-content: space-evenly;
  margin-right: auto;
}

.mainMenu > li {
  list-style: none;
}

.mainMenu > li > a {
  font-size: 0.9rem;
  font-weight: 600;
  color: rgba(255, 255, 255, 0.619);
  text-shadow: 1px 1px #1c1c1c;
  text-decoration: none;
}

.mainMenu > li > a:hover {
  color: #ffffff72;
}

.mainMenu > li > a.active {
  color: #ffffff;
  border-bottom: 2px solid white;
}

.searchDiv {
  margin: 0 10px 0 auto;
  display: flex;
  cursor: pointer;
}

.searchDiv > .searchFor {
  background: #141414;
  outline: none;
  border: none;
  text-align: center;
  border-radius: 20px;
  margin: 0 5px;
  width: 0;
  transition: 0.4s linear;
  color: white;
}

.searchDiv > .searchFor.active {
  width: 200px;
  border: 1px solid white;
  transition: 0.4s linear;
}

.searchImg {
  width: 20px;
}

.profile {
  width: 30px;
  margin-right: 30px;
}

.avatar {
  width: 30px;
}
